export default{
	props:{
		cinema: Object,
		movieId: Number,
		day: String || Number
	},
	computed:{
		url(){
			return `/pages/cinema-detail/cinema-detail?cinemaId=${this.cinema.id}&movieId=${this.movieId}&day=${this.day}`
		}
	}
}
